.glitch-off {
  color: #fff;
  letter-spacing: .3em;
  font-size: 4em;
  position: relative;
}

.glitch {
  color: #fff;
  letter-spacing: .3em;
  font-size: 4em;
  animation: glitch-skew 1s linear infinite alternate-reverse;
  position: relative;
}

.glitch:before {
  content: attr(data-text);
  width: 100%;
  height: 100%;
  text-shadow: -2px 0 #dd0e15;
  clip: rect(44px, 450px, 56px, 0);
  animation: glitch-anim 5s linear infinite alternate-reverse;
  position: absolute;
  top: 0;
  left: 2px;
}

.glitch:after {
  content: attr(data-text);
  width: 100%;
  height: 100%;
  text-shadow: -2px 0 #dd0e15, 2px 2px #dd0e15;
  clip: rect(44px, 450px, 56px, 0);
  animation: glitch-anim2 5s linear infinite alternate-reverse;
  position: absolute;
  top: 0;
  left: -2px;
}

